}.profile .content{display:flex;align-items:center}@media(max-width:1199.98px){.profile .content{justify-content:center}
}.profile .content h3{font-size:24px;font-weight:500}.profile .content h4{font-size:18px}
@media(max-width:991.98px){.related-blog h2{text-align:center}}.related-blog .badge{color:#5c7656;border:1px solid #5c7656}
.related-blog .card{box-shadow:none;border-radius:0;padding:48px 0 0 0}@media(max-width:1199.98px){.related-blog .card{padding:32px 0 0 0}
}@media(max-width:991.98px){.related-blog .card{text-align:center;padding:16px 0 0 0}
}.related-blog .card .blog-post-item-thumb{margin-bottom:24px;border-radius:16px;overflow:hidden}
@media(max-width:991.98px){.related-blog .card .blog-post-item-thumb{margin-bottom:32px}
}@media(max-width:991.98px){.related-blog .card .blog-post-content{margin-bottom:32px}
}.related-blog .card .blog-post-content .blog-post-meta span{font-weight:700;font-size:14px;color:#5c7656}
.related-blog .card .blog-post-content .blog-post-body-title a h3{font-size:24px;line-height:32px;margin-bottom:32px}
.related-blog .card .blog-post-content .blog-post-body-title a h3:hover{color:#5c7656}
.related-blog .card .blog-post-content .blog-post-body-title p{margin-bottom:32px}
.blog-details .badge{color:#5c7656;border:1px solid #5c7656}.blog-details .blog-featured-image{border-radius:8px}
.blog-details-content h2{font-size:30px;margin-bottom:24px}.blog-details-content p{line-height:170%;margin-bottom:32px}
.blog-details-content ul{margin:48px 0}.blog-details-content ul li{padding-left:40px;margin-bottom:32px;position:relative}
.blog-details-content ul li span{line-height:170%}.blog-details-content ul li:before{position:absolute;content:"";width:16px;height:16px;background:#153c3c;border-radius:100%;left:0;top:50%;transform:translateY(-50%)}
.pagination .page-item .btn i{vertical-align:middle}.pagination .page-item.active .btn-outline{background-color:#a0e1da;color:#0a2533 !important}
.pagination .page-item .page-total{margin-left:5px}.pagination .page-item:last-child{margin-left:15px}
.page-link{font-family:"Satoshi",sans-serif;position:relative;display:block;color:#173641;background-color:#fff;border:0;padding:6px 12px;border-radius:6px;font-weight:500;font-size:20px}
.page-link:hover{z-index:2;color:inherit;background-color:inherit}.page-link:focus{z-index:3;color:#5c7656;background-color:#fff;outline:0;box-shadow:none}
.page-item{margin-right:8px}.page-item:last-child{margin-right:0}.page-item:not(:first-child) .page-link{margin-left:0}
.footer{background-color:#0a2533}.footer-widget h6{color:#718284}